import React from "react";
import classes from "./Container2.module.scss";
import { NavLink } from "react-router-dom";

export const Container2: React.FC<{
  img: string;
  title: string;
  caption: string;
  position: number;
}> = ({ img, title, caption, position }) => {
  return (
    <div className={classes["container-2"]}>
      <div className={classes["container-2__aside-left"]}>
        <img
          src={img}
          alt="cakes"
          className={classes["container-2__img"]}
          style={{ objectPosition: `${position}% 50%` }}
        />
      </div>
      <div className={classes["container-2__aside-right"]}>
        <div className={classes["container-2__wrapper"]}>
          <h2 className={classes["container-2__title"]}>{title}</h2>
          <p className={classes["container-2__caption"]}>{caption}</p>
          <p className={classes["container-2__text"]}>
            I'm a paragraph. Click here to add your own text and edit me. It’s
            easy. Just click “Edit Text” or double click me to add your own
            content and make changes to the font.
          </p>
          <NavLink to="/flavors" className={classes["container-2__btn"]}>
            MENU
          </NavLink>
        </div>
      </div>
    </div>
  );
};
