import React from "react";
import classes from "./Container3.module.scss";
import { NavLink } from "react-router-dom";

export const Container3: React.FC<{
  img: string;
  title: string;
  caption: string;
  position: number;
}> = ({ img, title, caption, position }) => {
  return (
    <div className={classes["container-3"]}>
      <div className={classes["container-3__aside-left"]}>
        <div className={classes["container-3__wrapper"]}>
          <h3 className={classes["container-3__title"]}>{title}</h3>
          <p className={classes["container-3__caption"]}>{caption}</p>
          <p className={classes["container-3__text"]}>
            I'm a paragraph. Click here to add your own text and edit me. It’s
            easy. Just click “Edit Text” or double click me to add your own
            content and make changes to the font.
          </p>
          <NavLink to="/about" className={classes["container-3__btn"]}>
            READ MORE
          </NavLink>
        </div>
      </div>
      <div className={classes["container-3__aside-right"]}>
        <img
          src={img}
          alt="cakes"
          className={classes["container-3__img"]}
          style={{ objectPosition: `${position}% 50%` }}
        />
      </div>
    </div>
  );
};
